*{
    margin: 0;
    padding: 0;
    
}

.container{
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
}

.container-login{
    width: 100%;
    min-height: 100vh;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content:  center;

    padding: 15px;

    /* background-color: #ddd; */

}

.wraper-login{
    width: 390px;
    background-color: #fff;
    border-radius: 10px;

    overflow: hidden;

    padding: 77px 55px 33px 55px;

    box-shadow: 0 5px 10px 0px rgba(0,0,0,0.2);
}

.login-form{
    width: 100%;
}

.login-form-title{
    display: block;
    font-size: 30px;
    color: #375e65;

    line-height: 1.2;
    text-align: center;
}

.login-form-title img{
    width: 220px;
    margin-bottom: 30px;
}

.wraper-input{
    width: 100%;
    position: relative;
    border-bottom: 2px solid #375e65;
    margin-bottom: 37px;
}

.input{
    font-size: 15px;
    color: #000;
    line-height: 1.2;
    border: none;
    display: block;

    width: 100%;
    height: 45px;

    background-color: transparent;

    padding: 0 5px;
}

.eye{
    position: absolute;
    
}
.focus-input{
    position: absolute;
    display: block;
    width: 100%;

    height: 100%;

    top: 0;
    left: 0;

    pointer-events: none;
    color: #ddd;
}

.focus-input::before{
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;

    -webkit-transition:  all 0.4s;
    -o-transition: all 0.4s;
    -moz-transiotn: all 0.4s;

}

.focus-input::after{
    font-size: 15px;
    color: #375e65;
    line-height: 1.2;

    content: attr(data-placeholder);

    display: block;
    width: 100%;
    position: absolute;
    top: 16px;
    left: 0px;

    padding-left: 5px;

    -webkit-transition:  all 0.4s;
    -o-transition: all 0.4s;
    -moz-transiotn: all 0.4s;

}

.input:focus{
    outline: 0;
}

.input:focus + .focus-input::after{
    top: -10px;
}

.input:focus + .focus-input::before{
    width: 100%;
}

.has-val + .focus-input::after{
    top: -10px;
}

.container-login-form-btn{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

.login-form-btn{
    font-size: 15px;
    border: none;
    border-radius: 10px;
    color: #fff;

    line-height: 1.2;
    text-transform: uppercase;
    display: flex;

    justify-content: center;
    align-items: center;

    width: 100%;
    height: 50px;

    background-color: #375e65;
}

.login-form-btn:hover{
    cursor: pointer;
}

.account-info{
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 50px;
}

.info1{
    font-size: 12px;
    color: #000;
    line-height: 1.5;
    padding-right: 5px;
    text-decoration: none;
}

.info2{
    font-size: 12px;
    line-height: 1.5;
    text-decoration: none;
}