    flex-direction: column;
    align-items: center;
}

.formLogin div .txtUser {
    width: 440px;
    height: 30px;
    font-size: 18px;
    border: solid 0px #FFF;
    border-bottom: solid 3px #375E65;
    margin: 60px 0;
    background: url(../src/assets/user.svg) no-repeat right;
    background-size: 21px;
    color: #375E65;
}

.formLogin div .txtPass {
    width: 440px;
    height: 30px;
    font-size: 18px;
    border: solid 0px #FFF;
    border-bottom: solid 3px #375E65;
    margin-bottom: 40px;
