    color: #FFF;
}

.formLogin .btnEntrar:hover {
    background-color: #21383D;
    transition: ease-in-out 0.1s;
}

.styled-chkbox {
    display: block;
    position: relative;
    padding-left: 20px;
    /* margin-bottom: 50px; */
    cursor: pointer;
    font-size: 18px;
}

.styled-chkbox input {
    position: absolute;
    opacity: 0;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 2px;
    left: 0;
    height: 10px;
    width: 10px;
    border: solid 2px #375E65;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.styled-chkbox:hover input~.checkmark {
    background-color: #C7D2D4;
}

/* When the checkbox is checked, add a blue background */
.styled-chkbox input:checked~.checkmark {
    background-color: #375E65;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.styled-chkbox input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.styled-chkbox .checkmark:after {
    left: 2px;
    top: -1px;
    width: 3px;
    height: 7px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

