/*SIDE MENU*/
/**************************************************/

.side-menu {
    grid-column: 1/2;
    width: 225px;
    height: 100%;
    top: 0;
    display: block;
    z-index: 1;
}

.side-menu.inactive {
    width: 75px;
}

.side-menu .side {
    height: 100%;
    position: fixed;
    width: 225px;
    background-color: white;
    border-radius: 0px 20px 20px 0px;
    box-sizing: border-box;
    transition: all .2s ease-in-out;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.side-menu.inactive .side {
    width: 75px;
}

