/**************************************************/
/**************************************************/
/*MOBILE LAYOUT*/
/**************************************************/
/**************************************************/

@media screen and (max-width: 1500px) {

    .side-menu {
        position: absolute;
    }

    .side-menu.inactive {
        position: absolute;
        height: auto;
        width: 100%;
        z-index: 5;
        border-radius: 0px;
        box-shadow: none;
        /* box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3); */
    }

    .side-menu .side {
        z-index: 5;
        box-shadow: 0px 0px 5px 100vw rgba(0, 0, 0, 0.7);
    }

    .side-menu.inactive .side {
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }

    .side-menu .drawer {
        display: none;
    }

    .side-menu.inactive .drawer {
        display: inline-block;
    }

    .side-menu.inactive .side {
        left: -225px;
        transform: translateX(-225px);
        transition: all .2s ease-in-out;
    }

    /**************************************************/
    /*DRAWER*/
    /**************************************************/

    .drawer {
        top: 0;
        position: fixed;
        display: block;
        height: 50px;
        width: 100%;
        background-image: url(../src/assets/bg.svg);
        background-color: #375E65;
    }

    .drawer img {
        cursor: pointer;
        padding-top: 10px;
        padding-left: 10px;
    }

    .drawer label {
        color: #FFF;
        margin-left: 10px;
        font-size: 24px;
    }

    /**************************************************/
    /*CONTAINER*/
    /**************************************************/

    .container {
        z-index: 0;
        margin-top: 50px;
        margin-left: -20px;
    }

    /**************************************************/
    /*FILTRO*/
    /**************************************************/

    .container .filtro {
        z-index: 1;
        position: fixed;
        margin: 20% 10% 10% 10%;
        width: 70%;
        height: 70%;
        border-radius: 20px;
        box-shadow: 0px 0px 5px 100vw rgba(0, 0, 0, 0.7);
    }

    .filtro.inactiveFilter {
        margin: 10px;
        right: 0;
        bottom: 0;
        border-radius: 100px;
        width: 25px;
        height: 25px;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
    }

    .filtro img {
        cursor: pointer;
        /* padding: 2px; */
    }

    .filtro.inactiveFilter img {
        padding: 2px;
    }

    .filtro.inactiveFilter img:hover {
        filter: brightness(0.9);
    }

    .container .filtro .fechar {
        cursor: pointer;
        display: block;
        position: absolute;
        margin: 3%;
        top: 0;
        right: 0;
    }

    .container .filtro .fechar:hover {
        filter: brightness(0.9)
    }

    .filtro.inactiveFilter .fechar {
        display: none;
    }

    .filtro.inactiveFilter label {
        display: none;
    }

    .container .filtro .campos {
        /* position: absolute;
        bottom: 0; */
        margin: 40px;
        margin-top: 10%;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 16px;
        padding: 10px;
    }

    .filtro.inactiveFilter .campos {
        display: none;
    }

    .campos div {
        display: flex;
        flex-direction: column;
        padding: 5px;
        width: 100%;
    }

    .campos input {
        position: absolute;
        bottom: 0;
        margin-bottom: 10%;
        margin-left: 0px;
        height: 30px;
        /* width:  80%; */
    }

    /**************************************************/
    /*CONTENT*/
    /**************************************************/

    .soon {
        margin-top: 20px;
        height: 820px;
    }

    .content-top {
        flex-direction: column;
    }

    .twoinone {
        width: 100%;
    }

    .twoinonedt {
        width: 100%;
    }

    .content-bottom {
        flex-direction: column;
    }

    .twoinone {
        margin-bottom: 20px;
    }

    .twoinonedt {
        margin-bottom: 20px;
    }

    .lc {
        width: 100%;
        margin-bottom: 20px;
    }

    .t {
        width: 100%;
    }


}

@media screen and (max-width: 1400px) {
    .contentLogin {
        width: 100vw;
        height: 100vh;
    }

    .formLogin {
        width: 100%;
        height: 100%;
        border-radius: 0px;
    }

    .headerLogin {
        display: none;
    }

    .formLogin .txtUser {
